.feedback-menu {
  display: none;
  width: 320px;
  height: 100%;
  background-color: #ffffff;
  position: fixed;
  overflow-y: auto;
  right: 0;
  top: 0;
  z-index: 7;
  transition: 0.5s;
  -webkit-animation-name: feedback-menu_animation;
          animation-name: feedback-menu_animation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2); }

@-webkit-keyframes feedback-menu_animation {
  from {
    opacity: 0.1; }
  to {
    opacity: 1; } }

@keyframes feedback-menu_animation {
  from {
    opacity: 0.1; }
  to {
    opacity: 1; } }

.feedback-menu__open {
  display: block; }

.feedback-menu__cross {
  margin: 24px auto 40px 16px;
  background-image: url(./static/cross.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.feedback-menu-title {
  font-family: 'TT Lakes-Bold', sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1b1c21;
  margin-left: 16px;
  margin-bottom: 24px; }

.feedback-menu__highlight {
  position: absolute;
  top: 100px; }

.feedback-menu__field {
  width: 288px;
  height: 48px;
  background: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  margin: 0 16px 16px 16px;
  padding: 12px 16px; }

.field__text {
  font-family: 'TT Lakes-Medium', sans-serif;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #b5b6bc; }

.field__text-message {
  height: 118px; }

.feedback-menu-content {
  font-family: 'TT Lakes-Medium', sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  margin: 0 16px 19px 16px; }

.feedback-menu__send-order {
  position: relative;
  margin-right: 16px;
  margin-left: auto;
  display: flex;
  align-items: center;
  width: 119px;
  height: 32px;
  border-radius: 16px;
  background-color: #ff3e79;
  justify-content: space-between; }

.feedback-menu__send-text {
  font-family: 'TT Lakes-Bold', sans-serif;
  font-size: 12px;
  line-height: 24px;
  color: #ffffff;
  margin-left: 15px; }

.feedback-menu__send-icon {
  background-image: url(./static/order.svg);
  background-repeat: no-repeat;
  height: 10px;
  width: 7px;
  margin-right: 15px; }

.modal__area-input {
  border: 1px solid #b5b6bc;
  border-radius: 6px;
  margin: 0 0 20px 0;
  padding: 0 0 0 20px; }
  .modal__area-input::-webkit-input-placeholder {
    font-family: 'TT Lakes-Medium', sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #b5b6bc; }
  .modal__area-input::-moz-placeholder {
    font-family: 'TT Lakes-Medium', sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #b5b6bc; }
  .modal__area-input:-ms-input-placeholder {
    font-family: 'TT Lakes-Medium', sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #b5b6bc; }
  .modal__area-input::placeholder {
    font-family: 'TT Lakes-Medium', sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #b5b6bc; }

.area-input__size {
  height: 48px;
  width: 288px;
  margin-left: 16px;
  margin-right: 16px; }

.area-textarea__size {
  height: 118px;
  width: 288px;
  margin-left: 16px;
  margin-right: 16px;
  padding-top: 10px; }

@media (min-width: 1440px) {
  .feedback-menu {
    width: 440px; }
  .feedback-menu__cross {
    position: absolute;
    left: -100px;
    top: 20px; }
  .feedback-menu-title {
    margin-top: 50px;
    margin-left: 32px; }
  .feedback-menu__highlight {
    top: 45px; }
  .area-input__size {
    width: 376px;
    margin-left: 32px;
    margin-right: 32px; }
  .area-textarea__size {
    width: 376px;
    margin-left: 32px;
    margin-right: 32px; }
  .feedback-menu-content {
    margin: 0 32px 23px 32px; }
  .feedback-menu__send-order {
    margin-right: 32px; } }
