@media (min-width: 768px) {
  .swiper-container {
    flex-wrap: wrap;
    justify-content: center; } }

@media (min-width: 1440px) {
  .swiper-container {
    flex-wrap: wrap;
    justify-content: flex-start; } }

.swiper-pagination {
  position: relative;
  margin: 5px 0 24px 0; }

@media (min-width: 768px) {
  .swiper-pagination {
    display: none; } }

.swiper-pagination-bullet {
  background: gray;
  margin-left: 16px;
  width: 12px;
  height: 12px; }
